import ReactMarkdown from "react-markdown";
import DownloadButton from "./DownloadButton";

export default function AIAnalysis({ analysis }) {
  return (
    analysis && (
      <div className="mt-6 p-4 bg-blue-100 shadow rounded">
        <h2 className="text-lg font-bold">AI Feedback</h2>
        <ReactMarkdown className="text-sm">{analysis}</ReactMarkdown>
        <DownloadButton content={analysis} filename="AI_Feedback.txt" />
      </div>
    )
  );
}
